.categoriesHeading{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 44px;
  .categoriesTitle{
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    line-height: 42px;
    margin: 0 30px 0 0;
    color: #000000;
  }
  input{
    background-color: #ffffff;
    border: 1px solid #d9d9d9;
    border-radius: 2px;
    font-size: 16px;
    line-height: 24px;
    background-image: url(../../assets/icon/search.svg);
    background-repeat: no-repeat;
    background-position: left 11px center;
    padding: 8px 0 8px 35px;
  }
}