.menu{
  .heading{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 44px;
  }
  .productCards{
    display: grid;
    column-gap: 25px;
    grid-template-columns: repeat(3, 1fr);
  }
  .titleMenu{
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    line-height: 42px;
    margin: 0 30px 0 0;
    color: #000000;
  }
  .ingrediens{
    color: #8c8c8c;
    font-size: 18px;
    line-height: 21px;
  }
}
.cardButtom{
  display: flex;
  align-items: center;
  margin-bottom: 50px;
}
.cardBtn{
  display: flex;
  align-items: center;
  padding: 8px 16px;
  margin-right: 50px;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  color: #ffffffff;
  font-size: 16px;
  line-height: 24px;
  background: #1890ff;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.0015);
  .svg{
    display: inline-block;
    width: 24px;
    height: 24px;
    background-color: #ffffffff;
    background-repeat: no-repeat;
    -webkit-mask: url("../../assets/icon/shopping-cart.svg") no-repeat 50% 50%;
    mask: url("../../assets/icon/shopping-cart.svg") no-repeat 50% 50%;
  }
}